<script>
	export default {
		props:{
			show: Boolean,
			mask:Object
		},
	}
</script>

<template>
	<Transition name="modal">
		<div class="modal-mask" v-if="show" @click.self="$emit('close')" >
			<div class="modal-mask-container">
				<div class="modal-mask-container-header">
					<h4 class="modal-mask-container-header-title">Изменение задачи</h4>
					<button class="modal-mask-container-header-button" @click="$emit('close')">
						<svg class="modal-mask-container-header-button-img" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
							<path d="M6 18.9999C5.74414 18.9999 5.48828 18.9022 5.29297 18.7069C4.90234 18.3163 4.90234 17.6835 5.29297 17.2929L17.293 5.29285C17.6836 4.90222 18.3164 4.90222 18.707 5.29285C19.0977 5.68348 19.0977 6.31629 18.707 6.70691L6.70703 18.7069C6.51172 18.9022 6.25586 18.9999 6 18.9999Z" fill="#B6B6B6"/>
							<path d="M18 18.9999C17.7441 18.9999 17.4883 18.9022 17.293 18.7069L5.29297 6.70691C4.90234 6.31628 4.90234 5.68347 5.29297 5.29285C5.6836 4.90223 6.31641 4.90222 6.70703 5.29285L18.707 17.2928C19.0977 17.6835 19.0977 18.3163 18.707 18.7069C18.5117 18.9022 18.2559 18.9999 18 18.9999Z" fill="#B6B6B6"/>
						</svg>
					</button>
				</div>
				<div class="modal-mask-container-body">
					<input class="modal-mask-container-body-input"
						@input="$emit('update:valueInput',$event.target.value)"
						@keypress.enter="$emit('ApplyTaskModal', mask)"
						:value="mask.title" 
						type="text"
					>
					<div class="modal-mask-container-body-box">
						<div class="modal-mask-container-body-box-condition" v-if="mask.status === 'open'">
							<button class="modal-mask-container-body-box-condition-button" @click="$emit('updateStatusInWork', mask)">В работу</button>
							<button class="modal-mask-container-body-box-condition-button" @click="$emit('updateStatusClose', mask)">Закрыть</button>
						</div>
						<div class="modal-mask-container-body-box-condition" v-else-if="mask.status === 'inWork'">
							<button class="modal-mask-container-body-box-condition-button" @click="$emit('updateStatusOpen', mask)">Отложить</button>
							<button class="modal-mask-container-body-box-condition-button" @click="$emit('updateStatusClose', mask)">Закрыть</button>
						</div>
						<div class="modal-mask-container-body-box-condition" v-else-if="mask.status === 'close'">
							<button class="modal-mask-container-body-box-condition-button" @click="$emit('updateStatusOpen', mask)">Переоткрыть</button>
						</div>
					</div>
				</div>
				<div class="modal-mask-container-footer">
					<button class="modal-mask-container-footer-button modal-mask-container-footer-button__apply" 
						@click="$emit('ApplyTaskModal', mask)"
					>
						Применить
					</button>
					<button class="modal-mask-container-footer-button modal-mask-container-footer-button__delete" 
						@click="$emit('DeleteTaskModal', mask)"
					>
						Удалить задачу
					</button>
				</div>
			</div>
		</div>
	</Transition>
</template>

<style lang="scss" scoped>
	.modal-mask {
		background-color: rgba(0, 0, 0, 0.5);
		transition: opacity 0.3s ease;
		position: fixed;
		display: flex;
		height: 100%;
		z-index: 10;
		width: 100%;
		left: 0;
		top: 0;

		&-container {
			box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
			transition: all 0.3s ease;
			background-color: #fff;
			align-items: flex-start;
			flex-direction: column;
			border-radius: 16px;
			position: relative;
			display: flex;
			padding: 24px;
			margin: auto;
			width: 375px;
			z-index: 100;
			gap: 24px;

			&-header {
				justify-content: space-between;
				align-items: center;
				margin-top: 0;
				display: flex;
				width: 100%;

				&-title {
					font-family: 'Montserrat', sans-serif;
					line-height: 144.444%;
					font-weight: 700;
					font-size: 18px;
					color: #333;
					margin: 0;
				}
				&-button {
					background: none;
					padding: 0;
				}
			}
			&-body {
				align-items: center;
				flex-wrap: wrap;
				display: flex;
				width: 100%;

				&-input {
					padding: 16px 73px 16px 16px;
					border: 1px solid #F4F4F4;
					border-radius: 24px;
					margin-bottom: 16px;
					flex: 1 1 auto;

					&::placeholder {
						line-height: 166.667%;
						font-size: 12px;
						color: #333;
					}
				}
				&-box {
					align-items: center;
					position: relative;
					margin-top: 16px;
					flex: 1 1 100%;
					display: flex;
					gap: 8px;

					&::before {
						background-color: #EAEAEA;
						border-radius: 10px;
						position: absolute;
						content: '';
						height: 2px;
						width: 100%;
						top: -16px;
						left: 0;
					}
				}
			}
			&-footer {
				justify-content: space-between;
				align-items: center;
				display: flex;
				width: 100%;

				&-button {
					line-height: 171.429%;
					border-radius: 50px;
					font-weight: 700;
					font-size: 14px;
					
					&__apply {
						background-color: #F60;
						padding: 8px 24px;
						color: #FFF;
					}
					&__delete {
						border: 2px solid #F60;
						background-color: #FFF;
						padding: 8px 32px;
						color: #F60;
					}
				}
			}
		}
	}
	.modal-default-button {
		float: right;
	}
	.modal-enter-from {
		opacity: 0;
	}
	.modal-leave-to {
		opacity: 0;
	}
	.modal-enter-from .modal-mask-container,
	.modal-leave-to .modal-mask-container {
		-webkit-transform: scale(1.1);
		transform: scale(1.1);
	}
</style>