<script>
import { ref } from 'vue';
import TaskBoard from './components/TaskBoard.vue';
import TaskCurrent from './components/TaskCurrent.vue';
import TaskInput from './components/TaskInput.vue';
import TaskModal from './components/TaskModal.vue';
import TaskTable from './components/TaskTable.vue';

export default {
	name: 'App',
	components: {
		TaskBoard,
		TaskCurrent,
		TaskInput,
		TaskModal,
		TaskTable
	},
	data() {
		return {
			valueInput:'',
			mask:{
				id:Number,
				title:String,
				status:'open'
			},
			openList:[
				{ id: 0., title: 'Сварить пельмени', status: 'open' },
				{ id: 1, title: 'Поднять инфрастуктуру проекта', status: 'open' },
			],
			inWorkList:[
				{ id: 2, title: 'Проснуться, улыбнуться, сделать отжимания, слетать на Марс и прочитать книгу', status: 'inWork' },
				{ id: 3, title: 'Поругаться с девопсом', status: 'inWork' },
			],
			closeList:[
			{ id: 4, title: 'Спеть - Знаешь ли ты, вдоль ночных дорог', status: 'close', }
			],
			List: [
				{ id: 0, title: 'Сварить пельмени', status: 'open', },
				{ id: 1, title: 'Поднять инфрастуктуру проекта', status: 'open', },
				{ id: 2, title: 'Проснуться, улыбнуться, сделать отжимания, слетать на Марс и прочитать книгу', status: 'inWork', },
				{ id: 3, title: 'Поругаться с девопсом', status: 'inWork', },
				{ id: 4, title: 'Спеть - Знаешь ли ты, вдоль ночных дорог', status: 'close', }
			],
		};
	},
	methods: {
		DeleteTaskModal(mask){
			let indexList = this.List.findIndex(c => c.id == mask.id);
			let indexOpenList = this.openList.findIndex(c => c.id == mask.id);
			let indexInWorkList = this.inWorkList.findIndex(c => c.id == mask.id);
			let indexCloseList = this.closeList.findIndex(c => c.id == mask.id);
			this.List.splice(indexList, 1);	
			if (indexOpenList > -1) {
				this.openList.splice(indexOpenList, 1)
			}
			if (indexInWorkList > -1) {
				this.inWorkList.splice(indexInWorkList, 1)
			}
			if (indexCloseList > -1) {
				this.closeList.splice(indexCloseList, 1)
			}
			this.showModal = false;
		},
		addTaskMask() {
			if (this.valueInput === '') return
			if(this.List.length > 0) {
				this.List.push({
					id: this.List[this.List.length-1].id + 1,
					title: this.valueInput,
					status:'open',
				});
			} else{
				this.List.push({
					id: 0,
					title: this.valueInput,
					status:'open',
				});
			}
			if(this.List.length > 0) {
				this.openList.push({
					id: this.List[this.List.length-1].id,
					title: this.valueInput,
					status:'open',
				});
			} else {
				this.openList.push({
					id: 0,
					title: this.valueInput,
					status:'open',
				});
			}
			this.valueInput = '';
		}, 
		ApplyTaskModal(mask){
			if(mask.status === 'open'){
				let indexOpenList = this.openList.findIndex(c => c.id == mask.id);
				let indexInWorkList = this.inWorkList.findIndex(c => c.id == mask.id);
				let indexCloseList = this.closeList.findIndex(c => c.id == mask.id);
				if (indexOpenList>-1) {
					this.openList.splice(indexOpenList, 1);					
				}
				if (indexInWorkList>-1) {
					this.inWorkList.splice(indexInWorkList, 1);					
				}
				if (indexCloseList>-1) {
					this.closeList.splice(indexCloseList, 1);					
				}
				this.openList.push({
					id: mask.id,
					title: mask.title,
					status: mask.status,
				});
			}
			if(mask.status === 'inWork'){
				let indexOpenList = this.openList.findIndex(c => c.id == mask.id);
				let indexInWorkList = this.inWorkList.findIndex(c => c.id == mask.id);
				let indexCloseList = this.closeList.findIndex(c => c.id == mask.id);
				if (indexOpenList>-1) {
					this.openList.splice(indexOpenList, 1);					
				}
				if (indexInWorkList>-1) {
					this.inWorkList.splice(indexInWorkList, 1);					
				}
				if (indexCloseList>-1) {
					this.closeList.splice(indexCloseList, 1);					
				}
				this.inWorkList.push({
					id: mask.id,
					title: mask.title,
					status: mask.status,
				});
			}
			if(mask.status === 'close'){
				let indexOpenList = this.openList.findIndex(c => c.id == mask.id);
				let indexInWorkList = this.inWorkList.findIndex(c => c.id == mask.id);
				let indexCloseList = this.closeList.findIndex(c => c.id == mask.id);
				if (indexOpenList>-1) {
					this.openList.splice(indexOpenList, 1);					
				}
				if (indexInWorkList>-1) {
					this.inWorkList.splice(indexInWorkList, 1);					
				}
				if (indexCloseList>-1) {
					this.closeList.splice(indexCloseList, 1);					
				}
				this.closeList.push({
					id: mask.id,
					title: mask.title,
					status: mask.status,
				});
			}
			this.valueInput = '';
			this.showModal = false;
		},	
		open(mask){
			this.showModal = true;
			this.mask = mask
		},
		close(){
			this.showModal = false;
      this.valueInput = "";
		},
		resetInput() {
      this.valueInput = "";
    },
		updateStatusOpen(mask){
			mask.status = 'open'
		},
		updateStatusInWork(mask){
			mask.status = 'inWork'
		},
		updateStatusClose(mask){
			mask.status = 'close'
		},
	},
  computed: {
  evenNumbers() {
		return this.List.slice(0,5)
  },
},
	setup() {
		const windowWidth = ref(window.screen.width)
		const showModal = ref(false)
		return {
			windowWidth,
			showModal,
		}
	}
}
</script>

<template>
	<main>
		<TaskModal  
			v-model:valueInput="mask.title" 
			@updateStatusOpen="updateStatusOpen"
			@updateStatusInWork="updateStatusInWork"
			@updateStatusClose="updateStatusClose"
			@keypress.enter="ApplyTaskModal"
			@addTaskMask="addTaskMask" 
			@ApplyTaskModal="ApplyTaskModal"
			@DeleteTaskModal="DeleteTaskModal"
			@close="close" 
			:show="showModal" 
			:mask="mask"
		/>
		<div class="top">
			<div class="top-container container">
				<h1 class="top-container-title">ToDo List Scloud</h1>
				<div class="top-container-box">
					<TaskCurrent 
						:openList="openList" 
						:inWorkList="inWorkList" 
						:closeList="closeList" 
					/>
					<TaskInput 
						v-model:valueInput="valueInput" 
						@addTaskMask="addTaskMask" 
						@keypress.enter="addTaskMask"
						@resetInput="resetInput"
						:windowWidth="windowWidth" 
					/>
				</div>
				<TaskTable 
					@open="open" 
					:windowWidth="windowWidth" 
					:List="List" 
					:evenNumbers="evenNumbers"
				/>
			</div>
		</div>
		<TaskBoard 
			v-if="windowWidth>768" 
			:mask="mask"
			:openList="openList" 
			:inWorkList="inWorkList" 
			:closeList="closeList" 
		/>
	</main>
	<footer class="footer">
		<div class="footer-body"></div>
	</footer>
</template>