<script >
export default {
	data() {
    return {
			isOpen: true,
      ref: null
    };
  },

	props: {
		evenNumbers:Array,
		List:Array,
		windowWidth:Number,
	},
	methods: {
		handleClick() {
			if (this.isOpen === false) {
				this.$refs.targetRef.scrollIntoView({ behavior: 'smooth' });				
			}
			this.isOpen = !this.isOpen
    }
	}
}
</script>

<template>
	<div class="table" ref="targetRef">
		<table class="table-desktop" v-if="windowWidth>475">
			<thead class="table-desktop-header">
				<tr class="table-desktop-header-title">
					<th class="table-desktop-header-title-left">Задачи</th>
					<th class="table-desktop-header-title-right">Статус</th>
				</tr>
			</thead>
			<tbody class="table-desktop-body">
				<div class="table-desktop-body-list">
					<tr class="table-desktop-body-list-item" v-for="mask in List" v-show="!isOpen" :key="mask.id" >			
						<td class="table-desktop-body-list-item-title">{{ mask.title }}</td>
						<td class="table-desktop-body-list-item-buttons">
							<button class="table-desktop-body-list-item-buttons-button" @click="$emit('open', mask)" v-if="mask.status === 'open'">Открыт</button>
							<button class="table-desktop-body-list-item-buttons-button" @click="$emit('open', mask)" v-else-if="mask.status === 'inWork'">В работе</button>
							<button class="table-desktop-body-list-item-buttons-button" @click="$emit('open', mask)" v-else-if="mask.status === 'close'">Закрыт</button>
						</td>
					</tr>
				</div>
				<div class="table-desktop-body-list">
					<tr class="table-desktop-body-list-item" v-for="mask in evenNumbers" v-show="isOpen" :key="mask.id">
						<td class="table-desktop-body-list-item-title">{{ mask.title }}</td>
						<td class="table-desktop-body-list-item-buttons">
							<button class="table-desktop-body-list-item-buttons-button" @click="$emit('open', mask)" v-if="mask.status === 'open'">Открыт</button>
							<button class="table-desktop-body-list-item-buttons-button" @click="$emit('open', mask)" v-else-if="mask.status === 'inWork'">В работе</button>
							<button class="table-desktop-body-list-item-buttons-button" @click="$emit('open', mask)" v-else-if="mask.status === 'close'">Закрыт</button>
						</td>
					</tr>
				</div>
			</tbody>
		</table>
		<div class="table-mobile" v-if="windowWidth<476">
			<h4 class="table-mobile-title">Задачи</h4>
			<div class="table-mobile-list">
				<div class="table-mobile-list-item" v-show="isOpen" v-for="(mask) in evenNumbers" :key="mask.id">
					<span class="table-mobile-list-item-title">{{ mask.title }}</span>
					<button class="table-mobile-list-item-button" @click="$emit('open', mask)" v-if="mask.status === 'open'">Открыт</button>
					<button class="table-mobile-list-item-button" @click="$emit('open', mask)" v-else-if="mask.status === 'inWork'">В работе</button>
					<button class="table-mobile-list-item-button" @click="$emit('open', mask)" v-else-if="mask.status === 'close'">Закрыт</button>
				</div>
				<div class="table-mobile-list-item" v-show="!isOpen" v-for="(mask) in List" :key="mask.id">
					<span class="table-mobile-list-item-title">{{ mask.title }}</span>
					<button class="table-mobile-list-item-button" @click="$emit('open', mask)" v-if="mask.status === 'open'">Открыт</button>
					<button class="table-mobile-list-item-button" @click="$emit('open', mask)" v-else-if="mask.status === 'inWork'">В работе</button>
					<button class="table-mobile-list-item-button" @click="$emit('open', mask)" v-else-if="mask.status === 'close'">Закрыт</button>
				</div>
			</div>
		</div> 
		<button class="table-button-more" @click="handleClick" v-if="List.length > 5">
			<span class="table-button-more-text" v-if="isOpen">Показать еще</span>
			<svg class="table-button-more-img" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
				<path d="M12 15.5C11.7441 15.5 11.4883 15.4023 11.293 15.207L6.29297 10.207C5.90234 9.8164 5.90234 9.18359 6.29297 8.79297C6.6836 8.40235 7.31641 8.40234 7.70703 8.79297L12 13.0859L16.293 8.79297C16.6836 8.40234 17.3164 8.40234 17.707 8.79297C18.0977 9.1836 18.0977 9.81641 17.707 10.207L12.707 15.207C12.5117 15.4024 12.2559 15.5 12 15.5Z" fill="#FF6600"/>
			</svg>
			<span class="table-button-more-text" v-if="!isOpen">Скрыть</span>
			<svg class="table-button-more-img-reverse" width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
				<path d="M12 15.5C11.7441 15.5 11.4883 15.4023 11.293 15.207L6.29297 10.207C5.90234 9.8164 5.90234 9.18359 6.29297 8.79297C6.6836 8.40235 7.31641 8.40234 7.70703 8.79297L12 13.0859L16.293 8.79297C16.6836 8.40234 17.3164 8.40234 17.707 8.79297C18.0977 9.1836 18.0977 9.81641 17.707 10.207L12.707 15.207C12.5117 15.4024 12.2559 15.5 12 15.5Z" fill="#FF6600"/>
			</svg>
		</button>
	</div>
</template>

<style lang="scss" scoped>
	.table {
		&-desktop {
			background-color: #fff;
			border-radius: 48px;
			padding: 40px;
			width: 100%;

			&-header {
				&-title {
					&-left,
					&-right{
						padding-bottom: 20px;
					}
					&-left {
						text-align: start;
					}
					&-right {
						text-align: center;
					}
				}
			}
			&-body {
				&-list {
					display: contents;

					&-item {
						position: relative;
						text-align: left;

						&:not(:last-child)::after {
							content: '';
							width: 100%;
							height: 1px;
							bottom: 10px;
							left: 0;
							background-color: #EAEAEA;
							position: absolute;
						}
						&-title {
							width: 100%;
							padding-bottom: 20px;
						}
						&-buttons {
							text-align: center;

							&-button {
								white-space: nowrap;
							}
						}
					}
				}
			} 
		}
		&-button-more {
			display: inline-flex;
			padding: 12px 32px;
			justify-content: center;
			align-items: center;
			border-radius: 50px;
			border: 2px solid  #F60;
			background: #FFF;
			color:  #F60;
			font-weight: 700;
			line-height: 171.429%;
			font-size: 14px;
			margin-top: 40px;

			&-img-reverse{
				transform: rotateZ(180deg);
			}
		}
	}
	@media (max-width:768px) {
		.table {
			&-desktop {
				&-body {
					&-list {
						&-item {
							&-buttons {
								&-button-more {
									margin-top: 32px;
								}
							}
						}
					}
				}
			}
		}
	}
	@media (max-width:475px) {
		.table {
			&-mobile {
				&-title {
					font-family: 'Montserrat', sans-serif;
					margin-bottom: 32px;
					line-height: 140%;
					font-weight: 700;
					font-size: 20px;
					color: #333;
				}
				&-list {
					flex-direction: column;
					align-items: center;
					display: flex;
					gap: 24px;

					&-item {
						align-items: flex-start;
						justify-content: center;
						flex-direction: column;
						border-radius: 32px;
						background: #FFF;
						display: flex;
						padding: 24px;
						width: 100%;
						gap: 20px;

						&-title {
							line-height: 150%;
							font-weight: 700;
							font-size: 16px;
							color: #333;
						}
						&-button {
							border: 2px solid #F60;
							background-color: #fff;
							line-height: 171.429%;
							border-radius: 50px;
							padding: 12px 32px;
							font-weight: 700;
							font-size: 14px;
							color: #F60;
							width: 100%;
						}
					}
				}
			}
			&-button-more {
				margin-top: 24px;
			}
		}
	}
</style>